import { Tooltip, TooltipProps } from "@mui/material"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

const RPTooltip = (
  props: TooltipProps & { maxWidth?: number | string; disabledContent?: boolean },
) => {
  const { isHoverDevice, isMobile } = useDeviceInfo()
  const disableOnMobile = isMobile || !isHoverDevice
  const {
    arrow = true,
    disableInteractive = true,
    disableFocusListener = disableOnMobile,
    disableHoverListener = disableOnMobile,
    disableTouchListener = disableOnMobile,
  } = props

  const { maxWidth, disabledContent, children, ...forwarded } = props

  return (
    <Tooltip
      arrow={arrow}
      disableInteractive={disableInteractive}
      disableFocusListener={disableFocusListener}
      disableHoverListener={disableHoverListener}
      disableTouchListener={disableTouchListener}
      PopperProps={
        maxWidth
          ? {
              sx: {
                "& div": { maxWidth },
                ...props.PopperProps?.sx,
              },
              ...props.PopperProps,
            }
          : props.PopperProps
      }
      {...forwarded}
    >
      {disabledContent ? <span>{children}</span> : children}
    </Tooltip>
  )
}

export { RPTooltip }
